import { createBreak as createBreakApi, deleteVisit as deleteVisitApi, getVisitById as getVisitByIdApi } from '../../api/api';
import { modal, unmountModal } from '../modal';
import { updateVisitinDom, calculateVisitInfo } from './utilsVisitStyle';

export const createBreak = async targetCell => {
    const { locationId, visitDate, visitTime, employeeId } = calculateVisitInfo(targetCell);

    await createBreakApi({ locationId, visitDate, visitTime, employeeId });
    unmountModal();
    updateVisitinDom();
};

export const createVisit = targetCell => {
    const { visitDate, visitTime, employeeId } = calculateVisitInfo(targetCell);

    window.location.href = `https://bunny-nails-cafe.bubbleapps.io/version-test/c_visit?employee=${employeeId}&visitDate=${visitDate}&visitTime=${visitTime}`;
};

export const editVisit = visit => {
    const visitId = visit.getAttribute('id');
    window.location.href = `https://bunny-nails-cafe.bubbleapps.io/version-test/u_visit?visit=${visitId}`;
};

export const editVisitInWaitList = visit => {
    const visitId = visit.getAttribute('id');
    window.location.href = `https://bunny-nails-cafe.bubbleapps.io/version-test/waiting_list_upd?visit=${visitId}`;
};

export const deleteVisit = async visit => {
    const id = visit.getAttribute('id');
    await deleteVisitApi(id);
    unmountModal();
    updateVisitinDom();
};

export const getVisitById = async visit => {
    const id = visit.getAttribute('id');
    const visitTime = visit.querySelector('.visitTime').textContent;
    const data = await getVisitByIdApi(id);
    const { masterLock, visitCategories, visitItems, conflictState, client, documentState, clientState, visitLinkFirst, visitLinkSecond } = data;
    let visitStatus;
    visitCategories.forEach(item => {
        if (item?.name === '') {
            visitStatus = 'model';
        }
        if (item?.name === '') {
            visitStatus = 'employee';
        }
    });

    const visitLink = !!visitLinkFirst || !!visitLinkSecond || false;

    return { visitTime, masterLock, visitStatus, visitItems, conflictState, client, documentState, clientState, visitLink };
};

export const createVisitinWaitList = () => {
    window.location.href = ` https://bunny-nails-cafe.bubbleapps.io/version-test/waiting_list`;
};

export const warningsCheck = async id => {
    console.log(id);
    const data = await getVisitByIdApi(id);

    if (!data) {
        return false;
    }
    const { visitErrors } = data;

    if (!visitErrors.length) {
        return false;
    }

    modal({ visitErrors, type: 'warning' });
};

export const findScrollableParent = element => {
    if (!element) {
        return null;
    }

    if (element.scrollHeight > element.clientHeight) {
        //    
        return element;
    }

    //     
    return findScrollableParent(element.parentElement);
};
