import { initialState } from '../modules/inintialState';

const BASE_URL = 'https://api-dev.42wms.com/api';
const { bearerToken } = initialState;

export const request = async (url, method = 'GET', body = null) => {
    try {
        const headers = {
            Authorization: bearerToken,
            'Content-Type': 'application/json',
        };

        const result = await fetch(`${BASE_URL}/${url}`, {
            method,
            headers,
            body,
        });

        if (!result.ok) {
            const customError = new Error(`Could not fetch ${url}, status: ${result.status}`);
            customError.statusCode = result.status;
            throw customError;
        }

        if (result.status === 204 || result.status === 201) {
            return null;
        }

        return await result.json();
    } catch (error) {
        throw error;
    }
};

export const getAllVisits = async ({ locationId, period = 'DAY', visitDate, employeeId }) => {
    try {
        let res;
        if (period === 'DAY') {
            res = await request(`visits/calendar?locationId=${locationId}&period=${period}&visitDate=${visitDate}`);
        } else if (period === 'WEEK' && !!employeeId) {
            res = await request(`visits/calendar?locationId=${locationId}&period=WEEK&visitDate=${visitDate}&employeeId=${employeeId}
            `);
        }

        if (!!res) {
            return res;
        }
    } catch (error) {
        throw error;
    }
};

export const updateVisit = async ({ id, visitTime, visitDate, duration, employeeId, waitingListState = false }) => {
    try {
        const body = JSON.stringify({ id, visitTime, visitDate, duration, waitingListState, ...(!!employeeId && { employee: { id: employeeId } }) });

        await request('visits/calendar', 'PUT', body);
    } catch (error) {
        throw error;
    }
};

export const createBreak = async ({ locationId, visitDate, visitTime, employeeId }) => {
    try {
        const body = JSON.stringify({
            breakState: true,
            location: {
                id: locationId,
            },
            visitDate,
            visitTime,
            duration: 60,
            employee: {
                id: employeeId,
            },
        });

        await request('visits/calendar', 'POST', body);
    } catch (error) {
        throw error;
    }
};

export const deleteVisit = async id => {
    try {
        await request(`visits/${id}`, 'DELETE');
    } catch (error) {
        throw error;
    }
};

export const getVisitById = async id => {
    try {
        const data = await request(`visits/${id}`);
        return data;
    } catch (error) {
        throw error;
    }
};
