import { findTargetCell, rechekVisitData } from '../utils';
import { unmountModal, unmountInfoModal } from '../modal';
import { store } from '../inintialState';

export const DragAndDrop = () => {
    const calendar = document.querySelector('.calendar');

    const dragstartHandler = event => {
        document.querySelector('.dragable')?.classList.remove('dragable');
        event.target.classList.add('dragable');
        event.dataTransfer.setData('text', event.target.getAttribute('id'));
        event.effectAllowed = 'move';

        let targetColumnID = event.target.closest('.calendarColumn')?.getAttribute('id');
        targetColumnID ||= event.target.closest('.waitList') && 'waitList';
        store.prevColumn = targetColumnID;
    };

    const dragoverHandler = event => {
        document.querySelector('.dragover')?.classList.remove('dragover');
        if (event.target.closest('.visit') || event.target.classList.contains('visit')) {
            const targetCell = findTargetCell({ x: event.clientX, y: event.clientY });
            targetCell?.classList.add('dragover');
        } else if (event.target.classList.contains('calendarCell')) {
            event.target.classList.add('dragover');
        } else if (event.target.classList.contains('waitList') || event.target.closest('.waitList')) {
            event.target.classList.add('dragover');
        }
        event.preventDefault();
    };

    const dropHandler = event => {
        event.preventDefault();
        const id = event.dataTransfer.getData('text');
        const visit = document.getElementById(id);
        let targetCell;
        if (event.target.classList.contains('calendarCell')) {
            targetCell = event.target;
        } else if (event.target.classList.contains('waitList') || event.target.closest('.waitList')) {
            targetCell = document.querySelector('.waitListBody');
        } else {
            targetCell = findTargetCell({ x: event.clientX, y: event.clientY });
        }

        targetCell.append(visit);

        let targetColumnID = visit.closest('.calendarColumn')?.getAttribute('id');
        targetColumnID ||= event.target.closest('.waitList') && 'waitList';
        store.currentColumn = targetColumnID;

        rechekVisitData(visit);
    };

    const dragendHandler = event => {
        document.querySelector('.dragable')?.classList.remove('dragable');
        event.dataTransfer.clearData();
        document.querySelector('.dragover')?.classList.remove('dragover');
    };

    calendar.addEventListener('dragstart', e => {
        if (e.target.classList.contains('visit')) {
            unmountModal();
            unmountInfoModal();
            dragstartHandler(e);
        }
    });

    calendar.addEventListener('dragover', e => {
        if (e.target.classList.contains('calendarCell') || e.target.closest('.calendarCell')) {
            dragoverHandler(e);
        } else if (e.target.classList.contains('waitList') || e.target.closest('.waitList')) {
            dragoverHandler(e);
        }
    });

    calendar.addEventListener('drop', e => {
        dropHandler(e);
    });

    calendar.addEventListener('dragend', e => {
        dragendHandler(e);
    });
};
