import { deleteVisit } from '../utils';
import { Icons } from '../icons';
import { unmountModal } from './modal';

export const modalDelete = element => {
    const item = element.classList.contains('break') || element.closest('.break') ? '' : '';

    const modalHTML = `
        <div class="modal deleteModal">
        <div class="modal-content">
            <h2></h2>
            <p>     ${item}</p>
            <div class="buttonGroup">
            <button class="reject">${Icons.cross} </button>
            <button class="access">${Icons.trash} </button>
            </div>
        </div>
    </div>
        `;
    document.querySelector('#root').insertAdjacentHTML('beforeend', modalHTML);
    document.body.style.overflow = 'hidden';
    document.querySelector('.reject')?.addEventListener('click', () => {
        unmountModal();
    });
    document.querySelector('.access')?.addEventListener('click', () => {
        const visit = element.closest('.visit') || element.classList.contains('visit');
        deleteVisit(visit);
        unmountModal();
    });
};
