import { generateWeek } from '../../utils';
import { createHeader } from './header';
import { createCalendarBody } from './body';

export const CalendarByEmployee = ({ startHour, finishHour, startDay, data }) => {
    const root = document.querySelector('#root');
    const calendarHTML = `<div class="calendar"></div>`;
    root.insertAdjacentHTML('afterbegin', calendarHTML);

    const daysOfWeek = generateWeek(startDay);

    createCalendarBody({ startHour, finishHour, daysOfWeek, data });
    createHeader(daysOfWeek);
};
