import { VisitTransformStyle, hexToRgba, calculateEndTime } from './utils';
import { Icons } from './icons';
const VISIT_COLORS = { dinner: '#DEDEDE', notAproove: '#8782C3', error: '#FF6B78', unknown: '#800080' };
const VISIT_HEADER_COLORS = { green: '#2DD3A1', purple: '#8782C3', dinner: '#8F8F8F', unknown: '#800080' };

const view = props => {
    const {
        id,
        duration,
        clientState,
        workplaceColor,
        client,
        visitTime,
        breakState,
        documentState,
        color,
        bookingSource,
        conflictState,
        visitErrors,
        visitItems,
        layerIndex,
        layerSize,
        masterLock,
        visitLinkFirst,
        visitLinkSecond,
    } = props;
    const cellHeight = document.querySelector('.calendarCell')?.clientHeight;

    const height = (duration / 30) * cellHeight + 1;
    const endTime = calculateEndTime({ visitTime, duration });
    let headerColor;
    let bgcColor;
    if (bookingSource === 'ADMINISTRATOR' && !breakState) {
        headerColor = VISIT_HEADER_COLORS.green;
        bgcColor = color || workplaceColor || VISIT_COLORS.unknown;
    } else if (bookingSource === 'ONLINE' || bookingSource === 'MOBILE_APP') {
        headerColor = VISIT_HEADER_COLORS.purple;
        if (documentState === 'PREPARED') {
            bgcColor = VISIT_COLORS.notAproove;
        } else {
            bgcColor = color || workplaceColor || VISIT_COLORS.unknown;
        }
    } else if (!!breakState) {
        bgcColor = VISIT_COLORS.dinner;
        headerColor = VISIT_HEADER_COLORS.dinner;
    } else {
        bgcColor = color || workplaceColor || VISIT_COLORS.unknown;
        headerColor = VISIT_HEADER_COLORS.unknown;
    }

    if (!!visitErrors.length && documentState !== 'PREPARED') {
        for (let item of visitErrors) {
            const resolveState = item.resolveState;
            if (!!resolveState) {
                //    ,   ,    "resolveState" : true
                continue;
            }
            bgcColor = VISIT_COLORS.error;
        }
    }

    const textColor = bgcColor === '#eded53' ? '#000' : '#fff';

    return `
    <div id=${id} class="visit ${breakState ? 'break' : ''}" layerIndex=${layerIndex} layerSize=${layerSize} style="height: ${height}px; z-index: ${
        layerSize - layerIndex + 1
    }; background-color: ${hexToRgba(bgcColor, 0.8)}" draggable="true">
    <div class="visitContent">
    <div class="header" style="background-color: ${headerColor}">
    <div class="visitTime">${visitTime}-${endTime}</div>        
        <div class="headerIcons">       
        ${conflictState === 'CONFLICT' ? Icons.conflict : ''}
        ${conflictState === 'CUT' ? Icons.cut : ''}
        ${conflictState === 'REMAKE' ? Icons.dislike : ''}
        ${masterLock ? Icons.lock : Icons.unlock}
        ${documentState === 'WAITING' ? Icons.clock : ''}
        ${documentState === 'CONFIRMED' ? Icons.tick : ''}
        ${documentState === 'ARRIVED' ? Icons.plus : ''}
        ${documentState === 'NOT_ARRIVED' ? Icons.minus : ''}
        </div>
    </div>
    <div class="body" style="color: ${textColor}">
    <div class="bodyIconWrapper">
    ${!!clientState ? `<span class="bodyIcon">New</span>` : ''}
    ${!!visitLinkFirst || !!visitLinkSecond ? `<span class="bodyIcon">${Icons.paperClip}</span>` : ''}
    </div>
    <div class="client">${client?.firstName || ''} ${client?.lastName || ''}</div>
    <div class="phoneNumber">${client?.phone || ''}</div>
    <div class="description">${visitItems?.map(item => `<div>${item?.service?.name}</div>`).join('')}</div>
    </div>
    </div>
    <div class="resizeHandle"></div>
    </div>
    `;
};

export const Visit = ({ data, period }) => {
    if (period === 'DAY') {
        data.map(({ visits, fullName, id }) => {
            if (id === 'WAITING_LIST') {
                visits.map(item => {
                    const { visitDate } = item;
                    const targetCell = document.getElementById(`wl-${visitDate}`);
                    targetCell?.insertAdjacentHTML('beforeend', view(item));
                });
            } else {
                const [s, n = ''] = fullName.split(' ');

                visits.map(item => {
                    const searchId = `day-${s}_${n}_${item.visitTime}`;
                    const targetCell = document.getElementById(searchId);

                    targetCell?.insertAdjacentHTML('afterbegin', view(item));
                    const visit = document.getElementById(item.id);
                    VisitTransformStyle({ visit, targetCell });
                });
            }
        });
    } else if (period === 'WEEK') {
        data.map(({ visits, id }) => {
            if (id === 'WAITING_LIST') {
                visits.map(item => {
                    const { visitDate } = item;
                    const targetCell = document.getElementById(`wl-${visitDate}`);
                    targetCell?.insertAdjacentHTML('beforeend', view(item));
                });
            } else {
                visits.map(item => {
                    const searchId = `day-${item?.visitDate}_${item?.visitTime}`;
                    const targetCell = document.getElementById(searchId);
                    targetCell?.insertAdjacentHTML('afterbegin', view(item));
                    const visit = document.getElementById(item.id);
                    VisitTransformStyle({ visit, targetCell });
                });
            }
        });
    }
};
