import { createBreak, createVisit } from '../utils';

export const modalCreate = element => {
    const modalHTML = `<div class="modal">
        <div class="createVisit"> </div>
        <div class="createBreak"> </div>
        </div>`;

    element.insertAdjacentHTML('beforeend', modalHTML);
    const modal = document.querySelector('.modal');
    const width = element?.clientWidth > 150 ? element?.clientWidth : 150;
    modal.style.width = `${width}px`;

    document.querySelector('.createBreak')?.addEventListener('click', () => createBreak(element));
    document.querySelector('.createVisit')?.addEventListener('click', () => createVisit(element));
};
