export const createHeader = data => {
    const calendar = document.querySelector('.calendar');

    const widthItem = document.querySelector('.calendarCell')?.clientWidth;

    const dateCell = ({ date, datyMonth, weekday }) => {
        return `
    <div id="head_${date}" class="dateCell" style="width: ${widthItem}px">
    <div class="weekday"> ${weekday}</div>
    <div class="date"> ${datyMonth} </div>
    </div>
    `;
    };

    const calendarHeaderHTML = `<div class="calendarHeader"></div>`;
    calendar.insertAdjacentHTML('afterbegin', calendarHeaderHTML);
    const calendarHeader = document.querySelector('.calendarHeader');

    data.map(item => {
        calendarHeader.insertAdjacentHTML('beforeend', dateCell(item));
    });
};
