import { updateVisit, getAllVisits } from '../../api/api';
import { initialState, store } from '../inintialState';
import { WaitingList } from '../calendar';
import { modal } from '../modal';
import { Visit } from '../visit';
import { calculateEndTime } from './utils';
import { warningsCheck } from './utilsModal';

export const findTargetCell = ({ x, y }) => {
    return document.elementsFromPoint(x, y).find(item => item.classList.contains('calendarCell'));
};
export const updateVisitinDom = async () => {
    const { prevColumn, currentColumn } = store;
    const calendar = document.querySelector('.calendar');

    calendar.style.opacity = '0';
    if (currentColumn === 'waitList' || prevColumn === 'waitList') {
        document.querySelector('.waitList').remove();
    }
    document.querySelectorAll('.visit').forEach(item => item.remove());
    const data = await getAllVisits(initialState);
    calendar.style.opacity = '1';
    if (currentColumn === 'waitList' || prevColumn === 'waitList') {
        const wlData = data.find(item => item.id === 'WAITING_LIST');
        WaitingList(wlData);
    }
    Visit({ data, period: initialState.period });
};

export const calculateVisitInfo = targetCell => {
    let visitTime;
    let visitDate;
    const employeeId = targetCell?.parentNode.getAttribute('employeeId');
    const { locationId } = initialState;
    if (initialState.period === 'WEEK') {
        [, visitTime] = targetCell?.getAttribute('id').split('_');
        visitDate = targetCell?.parentNode?.getAttribute('id');
    } else if (initialState.period === 'DAY') {
        [, , visitTime] = targetCell?.getAttribute('id').split('_');
        visitDate = initialState.visitDate;
    }

    return { visitTime, visitDate, employeeId, locationId };
};

export const rechekVisitData = async visit => {
    const isWaitList = visit.closest('.waitList') || false;
    const isCalendar = visit.closest('.calendarGrid') || false;
    const id = visit?.getAttribute('id');
    let waitingListState = false;

    try {
        if (isWaitList) {
            waitingListState = true;
            await updateVisit({ id, waitingListState });
        } else if (isCalendar) {
            const targetCell = visit?.parentNode;
            if (!targetCell) {
                return false;
            }
            const cellHeight = targetCell?.clientHeight;
            const { visitDate, visitTime, employeeId } = calculateVisitInfo(targetCell);

            const duration = Math.round(visit?.clientHeight / cellHeight) * 30;
            const endTime = calculateEndTime({ visitTime, duration });
            visit.querySelector('.visitTime').textContent = `${visitTime}-${endTime}`;

            await updateVisit({ id, visitTime, visitDate, duration, waitingListState, employeeId });
        }
        warningsCheck(id);
    } catch (error) {
        if (Object.hasOwn(error, 'statusCode')) {
            modal({ statusCode: error.statusCode, type: 'error' });
        }
    } finally {
        updateVisitinDom();
    }
};

export const VisitTransformStyle = ({ visit, targetCell }) => {
    const cellWidth = targetCell?.clientWidth;
    const layerIndex = visit?.getAttribute('layerIndex');
    const layerSize = visit?.getAttribute('layerSize');
    const left = (layerSize - layerIndex) * 10;
    const right = (layerIndex - 1) * 10;
    const shiftWidth = cellWidth - (left + right);

    visit.style.width = `${shiftWidth}px`;
    visit.style.left = `${left}px`;
    visit.style.right = `${right}px`;
};

export const checkBGCforCEll = ({ curentCell, startHour, finishHour }) => {
    const [hour, min] = curentCell.split('-');
    let bgc;
    if (Number(hour) < Number(startHour) || Number(hour) > Number(finishHour)) {
        bgc = 'lightgrey';
    } else if (Number(hour) === Number(finishHour)) {
        bgc = 'lightgrey';
    } else {
        bgc = 'white';
    }
    return bgc;
};

export const hexToRgba = (hex, alpha) => {
    const r = parseInt(hex.slice(1, 3), 16);
    const g = parseInt(hex.slice(3, 5), 16);
    const b = parseInt(hex.slice(5, 7), 16);
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
};
