import { modalCreate } from './modalCreate';
import { modalDelete } from './modalDelete';
import { modalInfo } from './modalInfo';
import { modalError } from './modalError';
import { modalWarning } from './modalWarning';

export const unmountModal = () => {
    document.querySelectorAll('.modal').forEach(item => item.remove());
    document.body.style.overflow = 'auto';
};

export const unmountInfoModal = () => {
    document.querySelectorAll('.modalInfo').forEach(item => item.remove());
};

export const modal = ({ element, statusCode, visitErrors, type, timeout }) => {
    if (type === 'create') {
        modalCreate(element);
    } else if (type === 'delete') {
        modalDelete(element);
    } else if (type === 'error') {
        modalError(statusCode, timeout);
    } else if (type === 'warning') {
        modalWarning(visitErrors, timeout);
    } else {
        modalInfo(element);
    }
};
