export const createIntervalArray = ({ startHour, finishHour, intervalMinutes }) => {
    const result = [];
    let startTime = new Date();
    startTime.setHours(startHour);
    startTime.setMinutes(0);

    let endTime = new Date();
    endTime.setHours(finishHour);
    endTime.setMinutes(0);

    while (startTime < endTime) {
        const hours = startTime.getHours().toString().padStart(2, '0');
        const minutes = startTime.getMinutes().toString().padStart(2, '0');
        result.push(`${hours}-${minutes}`);
        startTime.setMinutes(startTime.getMinutes() + intervalMinutes);
    }

    return result;
};

export const generateWeek = startDay => {
    const maxDaysInWeek = 7;
    const FitstDay = new Date(startDay);
    const dates = [];

    for (let i = 0; i < maxDaysInWeek; i++) {
        const day = FitstDay.getDate();
        const dateClone = FitstDay;
        let date = i === 0 ? new Date(dateClone) : new Date(dateClone.setDate(day + 1));
        const datyMonth = date.toLocaleString('uk-UA', { day: 'numeric', month: 'long' });
        const weekday = date.toLocaleString('uk-UA', { weekday: 'long' });

        const d = date.getDate();
        const m = date.getMonth() + 1;
        const y = date.getFullYear();
        dates.push({ date: `${y}-${m < 10 ? `0${m}` : m}-${d < 10 ? `0${d}` : d}`, datyMonth, weekday });
    }
    return dates;
};

export const generateDayTitle = date => {
    const currentDate = new Date();

    const tomorrowDate = new Date(currentDate);
    tomorrowDate.setDate(currentDate.getDate() + 1);

    const yesterdayDate = new Date(currentDate);
    yesterdayDate.setDate(currentDate.getDate() - 1);

    const checkDate = new Date(date);

    if (yesterdayDate.toDateString() === checkDate.toDateString()) {
        return { title: '', color: '#FD1328' };
    } else if (currentDate.toDateString() === checkDate.toDateString()) {
        return { title: '', color: '#2F11E6' };
    } else if (tomorrowDate.toDateString() === checkDate.toDateString()) {
        return { title: '', color: '#E315DB' };
    } else {
        const title = checkDate.toLocaleString('uk-UA', { weekday: 'long' });
        return { title, color: '#000' };
    }
};

export const converDateFormatToDots = date => {
    const currentDate = new Date(date);
    const day = currentDate.getDate();
    const month = currentDate.getMonth() + 1;
    const year = currentDate.getFullYear();

    function padNumber(number) {
        return number < 10 ? `0${number}` : number;
    }

    return `${padNumber(day)}.${padNumber(month)}.${year}`;
};

export const calculateGap = () => {
    const columns = document.querySelectorAll('.calendarColumn');
    return columns[1].offsetLeft - (columns[0].offsetLeft + columns[0].offsetWidth);
};

export const scrollTable = () => {
    let index;
    const table = document.querySelector('.calendarGrid');
    const widthItem = document.querySelector('.calendarCell')?.clientWidth;
    const firstCellId = Array.from(document.querySelector('.is-visible')?.childNodes)
        ?.find(item => item.className === 'masterCell')
        ?.getAttribute('id');

    document.querySelectorAll('.masterCell').forEach((item, i) => {
        if (item.getAttribute('id') === firstCellId) {
            index = i;
        }
    });
    const gap = calculateGap();
    table.scrollLeft = widthItem * index + (gap + 2) * index;
};

export const calculateEndTime = ({ visitTime, duration }) => {
    const [hour, minutes] = visitTime.split('-');
    const endTimeHours = Math.floor((Number(hour) * 60 + Number(minutes) + Number(duration)) / 60);
    const endTimeMin = (Number(hour) * 60 + Number(minutes) + Number(duration)) % 60 || '00';
    return `${endTimeHours}-${endTimeMin}`;
};
