export const createHeader = data => {
    if (data.length <= 1) {
        return;
    }

    const calendar = document.querySelector('.calendar');

    const slider = `
    <section class="splide" aria-label="Splide Basic">
    <div class="splide__track">
            <ul class="splide__list"></ul>
    </div>
    </section>
    `;
    const masterCell = ({ photo, fullName, position, width }) => {
        const [s, n = ''] = fullName.split(' ');

        return `
    <div id="head_${s}_${n}" class="masterCell" style="width: ${width}px">
    <div class="photo"> ${!!photo ? `<img src=${photo} alt='photo' />` : ''}</div>
    <div class="name"> ${fullName}</div>
    <div class="position"> ${position}</div>
    </div>
    `;
    };

    const waitListWidth = document.querySelector('.waitList')?.clientWidth;

    const widthHeader = document.querySelector('.calendarGrid')?.clientWidth + waitListWidth + 12;
    const calendarHeaderHTML = `<div class="calendarHeader" style="width: ${widthHeader}px"></div>`;
    calendar.insertAdjacentHTML('afterbegin', calendarHeaderHTML);

    const calendarHeader = document.querySelector('.calendarHeader');
    calendarHeader.insertAdjacentHTML('afterbegin', slider);

    const sliderContent = document.querySelector('.splide__list');
    const sliderWrapper = document.querySelector('.splide');
    const width = document.querySelector('.calendarColumn').clientWidth;
    const bodyGrid = document.querySelector('.calendarGrid');
    sliderWrapper.style.width = `${bodyGrid.clientWidth}px`;

    data.map(item => {
        if (item.id !== 'WAITING_LIST') {
            sliderContent.insertAdjacentHTML('beforeend', `<li class="splide__slide">${masterCell({ ...item, width })}</li>`);
        }
    });
};
