import { getVisitById } from '../utils';
import { Icons } from '../icons';

export const modalInfo = async element => {
    const addModalPosition = element => {
        const scrollY = window.scrollY;
        const scrollX = window.scrollX;

        const { right, top } = element.getBoundingClientRect();
        const { right: rightWrapper, bottom: bottomWrapper } = document.querySelector('.calendarGrid').getBoundingClientRect();
        // bubble fix
        const rect = document.getElementById('root').getBoundingClientRect();
        const x = rect.left + scrollX;
        const y = rect.top + scrollY;

        const modalElement = document.querySelector('.modalInfo');
        setTimeout(() => {
            //  ,       
            modalElement.style.opacity = '1';
        }, 500);

        let leftPosition;
        let topPosition;

        const modlWidth = modalElement.clientWidth;
        const modlHeight = modalElement.clientHeight;
        if (right + modlWidth < rightWrapper) {
            leftPosition = right + 5 + scrollX - x;
        } else {
            const delta = rightWrapper - right;
            leftPosition = rightWrapper - modlWidth - 5 - element?.clientWidth - delta - x;
        }
        if (top + scrollY + modlHeight < bottomWrapper + scrollY) {
            topPosition = top + scrollY - y;
        } else {
            topPosition = bottomWrapper + scrollY - modlHeight - y;
        }
        modalElement.style.left = `${leftPosition}px`;
        modalElement.style.top = `${topPosition}px`;
    };

    const modalHTML = ({ visitTime, visitStatus, masterLock, visitItems, conflictState, documentState, client, clientState, visitLink }) => `
        <div class="modal modalInfo" >
        <div>${visitTime}</div>
        <hr/>
        ${
            !!masterLock
                ? `<div style="background-color: #ff6b78" class="visitStatus">${Icons.lock}  </div>`
                : `<div style='background-color: #82b468' class='visitStatus'>
                    ${Icons.unlock}   
                </div>`
        }
        ${visitStatus === 'model' ? `<div style="background-color: #ffc800;" class="visitStatus">${Icons.smile}  </div>` : ''}
        ${visitStatus === 'employee' ? `<div style="background-color: #8782c3;" class="visitStatus">${Icons.heart}  </div>` : ''}
        ${documentState === 'WAITING' ? `<div style="background-color: #FFC800;" class="visitStatus">${Icons.clock}  </div>` : ''}
        ${documentState === 'CONFIRMED' ? `<div style="background-color: #8782C3" class="visitStatus">${Icons.tick}  </div>` : ''}
        ${documentState === 'ARRIVED' ? `<div style="background-color: #82B468" class="visitStatus">${Icons.plus}  </div>` : ''}
        ${documentState === 'NOT_ARRIVED' ? `<div style="background-color: #FF6B78" class="visitStatus">${Icons.minus}   </div>` : ''}
        ${!!clientState ? `<div class="visitStatus whiteBgc">New</div>` : ''}
        ${!!visitLink ? `<div class="visitStatus whiteBgc">${Icons.paperClip} </div>` : ''}
        <hr/>
        <div class="visitItems">
        ${visitItems?.map(item => `<div>${item?.service?.name}</div>`).join('')}
        </div>
        <hr/>
        ${conflictState === 'CONFLICT' ? `<div class="conflictStatus">${Icons.conflict} </div>` : ''}
        ${conflictState === 'CUT' ? `<div class="conflictStatus">${Icons.cut} </div>` : ''}
        ${conflictState === 'REMAKE' ? `<div style="background-color: #ff762f;" class="conflictStatus">${Icons.dislike} </div>` : ''}
        ${conflictState !== 'NO_CONFLICT' ? '<hr/>' : ''}
        <div class="clientWrpper">
        <div class="client">${client?.firstName || ''} ${client?.lastName || ''}</div>
        <div class="phoneNumber">${client?.phone || ''}</div>
        </div>
        </div>`;

    const res = await getVisitById(element);

    document.querySelector('.calendarGrid').insertAdjacentHTML('beforeend', modalHTML(res));

    addModalPosition(element);
};
