import { modal, unmountModal, unmountInfoModal } from '../modal';
import { editVisit, editVisitInWaitList, createVisitinWaitList } from '../utils';
import { initialState } from '../inintialState';

export const calendarEventsListener = () => {
    const calendar = document.querySelector('.calendar');

    calendar?.addEventListener('click', event => {
        if (event.target.classList.contains('calendarCell')) {
            unmountModal();
        }
    });

    calendar?.addEventListener('dblclick', event => {
        if (event.target.closest('.visit') || event.target.classList.contains('visit')) {
            if (event.target.closest('.break') || event.target.classList.contains('break')) {
                return null;
            }

            if (event.target.closest('.header') || event.target.closest('.resizeHandle')) {
                return null;
            }

            const visit = event.target.closest('.visit') || event.target.classList.contains('visit');

            if (event.target.closest('.waitList')) {
                editVisitInWaitList(visit); //redirect
            } else if (event.target.closest('.calendarGrid')) {
                editVisit(visit); //redirect
            }
        }
    });

    calendar?.addEventListener('contextmenu', event => {
        if (event.target.classList.contains('calendarCell')) {
            event.preventDefault();
            unmountModal();
            const today = new Date();
            today.setHours(0, 0, 0);

            if (initialState.period === 'DAY' && new Date(initialState.visitDate) >= today) {
                modal({ element: event.target, type: 'create' }); // 'modal " "  " "'
            } else if (initialState.period === 'WEEK' && new Date(event.target.closest('.calendarColumn')?.getAttribute('id')) >= today) {
                modal({ element: event.target, type: 'create' }); // 'modal " "  " "'
            }
        } else if (event.target.closest('.visit') || event.target.classList.contains('visit')) {
            event.preventDefault();
            unmountModal();
            modal({ element: event.target, type: 'delete' }); // 'modal  /  '
        }
    });

    let prevElementID = null;
    calendar?.addEventListener('mouseover', event => {
        if (event.target.closest('.header') || event.target.classList.contains('header')) {
            if (event.target.closest('.break') || event.target.classList.contains('break')) {
                return null;
            }
            const currentElementID = event.target.closest('.visit') || event.target.classList.contains('visit').getAttribute('id');
            if (currentElementID === prevElementID) {
                return null;
            }

            const visit = event.target.closest('.visit') || event.target.classList.contains('visit');
            prevElementID = currentElementID;
            unmountModal();
            modal({ element: visit }); //'modal     '
        }
    });

    document.addEventListener('mouseout', event => {
        if (event.target.classList.contains('calendarCell') || event.target.closest('.body') || event.target.classList.contains('body')) {
            prevElementID = null;
            unmountInfoModal();
        }
        if (event.target.classList.contains('calendarGrid') || event.target.closest('.calendarGrid')) {
            return null;
        }
        if (event.target.classList.contains('waitList') || event.target.closest('.waitList')) {
            return null;
        }
        if (event.target.classList.contains('deleteModal') || event.target.closest('.deleteModal')) {
            return null;
        }
        if (event.target.classList.contains('warningModal') || event.target.closest('.warningModal')) {
            return null;
        }
        if (event.target.classList.contains('errorModal') || event.target.closest('.errorModal')) {
            return null;
        }
        prevElementID = null;
        unmountModal();
    });

    document.addEventListener('keydown', event => {
        if (event.key === 'Escape' || event.key === 'Esc') {
            unmountModal();
        }
    });

    document.querySelector('.btnwaitCreate').addEventListener('click', () => {
        createVisitinWaitList();
    });
};
