import { unmountModal } from './modal';

export const modalError = (statusCode, timeout = 5000) => {
    let erroroMessage;
    if (statusCode === 400) {
        erroroMessage = '        ';
    }

    const modalHTML = `
        <div class="modal errorModal">
        <div class="modal-content">
            <h2></h2>
            <p>,   .</p>
            ${!!erroroMessage ? `<div class="visitErrorsWrapper">${erroroMessage}</div>` : ''}
        </div>
    </div>
        `;
    document.querySelector('#root').insertAdjacentHTML('beforeend', modalHTML);
    document.body.style.overflow = 'hidden';

    setTimeout(unmountModal, timeout);
};
