import { generateDayTitle, converDateFormatToDots } from '../utils';

export const WaitingList = data => {
    const calendarBody = document.querySelector('.calendarBody');
    const maxHeight = document.querySelector('.calendarTimelapsColumn').clientHeight;

    const dates = Array.from(
        new Set(
            data?.visits.map(({ visitDate }) => {
                return visitDate;
            })
        )
    );

    const waitListHTML = `
    <div id="_" class="waitList" style="height: ${maxHeight}px">
    <div class="waitListHeader">
    <div class="title"> </div>
    <button class="btnwaitCreate">+</button>
    </div>
    <div id="waitList" class="waitListBody" ></div>
    </div>
    `;

    const calendarGroupHTML = id => {
        const { title, color } = generateDayTitle(id);
        return `<div id=wl-${id} class="calendarGroup">
        <div class="waitListHeader">
        <div class="title" style="color: ${color}">${title}</div>
        <div class="date">${converDateFormatToDots(id)}</div>
         </div></div>`;
    };

    calendarBody.insertAdjacentHTML('beforeend', waitListHTML);
    const waitListBody = document.querySelector('.waitListBody');

    dates.map(item => waitListBody.insertAdjacentHTML('beforeend', calendarGroupHTML(item)));
};
