import { createIntervalArray, checkBGCforCEll, calculateGap } from '../../utils';
import { createCalendarTimelaps } from '../timelps';
import { modal } from '../../modal';
import { WaitingList } from '../waitingList';

export const createCalendarBody = ({ startHour, finishHour, data }) => {
    const calendar = document.querySelector('.calendar');
    const intervalArray = createIntervalArray({ startHour, finishHour, intervalMinutes: 30 });

    const calendarBodyHTML = `<div class="calendarBody ">
    <div class="calendarGrid ${data?.length > 10 && 'calendarGridOverflow'}"></div></div>`;

    calendar.insertAdjacentHTML('beforeend', calendarBodyHTML);

    const calendarGrid = document.querySelector('.calendarGrid');

    const calendarColumnHTML = (id, employeeId) => `<div id=${id} employeeId=${employeeId} class="calendarColumn"></div>`;
    const calendarCell = (i, bgc) => `<div id="day-${i}" class="calendarCell" ${!!bgc && `style = "background-color: ${bgc}"`}></div>`;

    createCalendarTimelaps(intervalArray);

    data.map(item => {
        const [s, n = ''] = item?.fullName.split(' ');
        if (item?.id !== 'WAITING_LIST') {
            const id = `${s}_${n}`;
            calendarGrid.insertAdjacentHTML('beforeend', calendarColumnHTML(id, item?.id));
            const calendarColumn = document.getElementById(id);
            const dayType = item?.dayType;
            intervalArray.map(subitem => {
                let bgc;
                if (dayType === 'OFF_SCHEDULE') {
                    bgc = 'lightgrey';
                } else {
                    bgc = checkBGCforCEll({ curentCell: subitem, startHour: item?.startHour, finishHour: item?.finishHour });
                }
                calendarColumn.insertAdjacentHTML('beforeend', calendarCell(`${id}_${subitem}`, bgc));
            });
        } else {
            WaitingList(item);
        }
    });

    if (data.length <= 1) {
        modal({ visitErrors: [{ visitError: { name: '  !' } }, { visitError: { name: ' .' } }], type: 'warning', timeout: false });
        return;
    }

    const gap = data.length > 3 ? calculateGap() + 2 : 1;
    const widthColumn = document.querySelector('.calendarCell').clientWidth + gap;
    const quant = Math.floor((window.innerWidth - document.querySelector('.waitList').clientWidth - document.querySelector('.calendarTimelapsColumn').clientWidth) / widthColumn);
    const gridWidth = quant * widthColumn;

    data?.length > 10 ? (calendarGrid.style.maxWidth = `${gridWidth}px`) : null;
};
