import { createCalendarBody } from './body';
import { createHeader } from './header';

const createCalendar = ({ startHour, finishHour, data }) => {
    createCalendarBody({ startHour, finishHour, data });
    createHeader(data);
};

export const Calendar = ({ startHour, finishHour, data }) => {
    const root = document.querySelector('#root');
    const calendarHTML = `<div class="calendar"></div>`;
    root.insertAdjacentHTML('afterbegin', calendarHTML);

    createCalendar({ startHour, finishHour, data });
};
