export const createCalendarTimelaps = intervalArray => {
    const calendarTimelapsColumnHTML = `<div class="calendarTimelapsColumn"></div>`;
    const calendarTimelapsCell = text => `
    <div class="calendarTimelapsCell"><div class="calendarTimelapsText">${text}</div></div>`;
    const calendarBody = document.querySelector('.calendarBody');

    calendarBody.insertAdjacentHTML('afterbegin', calendarTimelapsColumnHTML);

    document.querySelectorAll('.calendarTimelapsColumn').forEach(item => {
        intervalArray.map((subitem, i) => {
            item.insertAdjacentHTML('beforeend', calendarTimelapsCell(subitem));
        });
    });
};
