import { unmountModal } from './modal';

export const modalWarning = (visitErrors, timeout = 3000) => {
    const modalHTML = `
        <div class="modal warningModal">
        <div class="modal-content">
            <h2></h2>
            <p>,  . 
            <div class="visitErrorsWrapper">${visitErrors.map(({ visitError }) => {
                return `<div>${visitError.name}</div>`;
            })}</div></p>
            <div class="buttonGroup">
            <button class="ok"></button>
            </div>
        </div>
    </div>
        `;
    document.querySelector('#root').insertAdjacentHTML('beforeend', modalHTML);
    document.body.style.overflow = 'hidden';
    document.querySelector('.ok')?.addEventListener('click', () => {
        unmountModal();
    });

    if (timeout) {
        setTimeout(unmountModal, timeout);
    }
};
