import '@babel/polyfill';
import Splide from '@splidejs/splide';
import '@splidejs/splide/css';
import './index.html';
import './index.scss';

import { getAllVisits } from './api/api.js';
import { Calendar, CalendarByEmployee } from './modules/calendar';
import { DragAndDrop, Resize, calendarEventsListener } from './modules/event-listeners';
import { Visit } from './modules/visit';
import { scrollTable, calculateGap } from './modules/utils';
import { modal } from './modules/modal';
import { initialState } from './modules/inintialState.js';

const getDataApiAll = async ({ locationId, visitDate, employeeId, period = 'DAY' }) => {
    try {
        const data = await getAllVisits({ locationId, visitDate, employeeId, period });
        console.log(data);
        if (!data.length) {
            console.log('data wasn`t get');
        } else {
            if (period === 'DAY') {
                Calendar({ startHour: initialState.startHour, finishHour: initialState.finishHour, data });
                if (data.length >= 2) {
                    const perPage = data.length <= 11 ? data.length : 10;
                    const gap = data.length > 3 ? calculateGap() + 2 : 1;
                    const slider = new Splide('.splide', { pagination: false, direction: 'ltr', perPage, arrows: false, gap: `${gap}px` }).mount();
                    slider.on('moved', () => {
                        scrollTable();
                    });
                }

                Visit({ data, period: 'DAY' });
                calendarEventsListener();
            } else if (period === 'WEEK') {
                CalendarByEmployee({ startHour: initialState.startHour, finishHour: initialState.finishHour, startDay: visitDate, data });
                Visit({ data, period: 'WEEK' });
                calendarEventsListener();
            }

            DragAndDrop();
            Resize();
        }
    } catch (error) {
        modal({ type: 'error' });
    }
};

getDataApiAll(initialState);

document.querySelector('#dayWeekBtn').addEventListener('click', () => {
    if (initialState.period === 'DAY') {
        initialState.period = 'WEEK';
    } else if (initialState.period === 'WEEK') {
        initialState.period = 'DAY';
    }
    document.querySelector('.calendar').remove();
    getDataApiAll(initialState);
});
