import { rechekVisitData } from '../utils';
import { store } from '../inintialState';

export const Resize = () => {
    const calendar = document.querySelector('.calendar');
    const heightItem = document.querySelector('.calendarCell')?.clientHeight;
    let initialVisitHeight = 0;
    let isResizing = false;
    let element;
    let visitElement;
    let startY;

    calendar.addEventListener('mousedown', e => {
        if (e.target.classList.contains('resizeHandle')) {
            e.stopPropagation();
            element = e.target;
            visitElement = element.parentNode;
            initialVisitHeight = visitElement.clientHeight;
            startY = e.clientY;
            isResizing = true;
        }
    });

    document.addEventListener('mousemove', e => {
        if (isResizing) {
            const step = 0.05;
            const deltaY = (e.clientY - startY) * step;
            const newHeight = Math.ceil((visitElement.clientHeight + deltaY) / step) * step;
            visitElement.style.height = `${newHeight}px`;
            visitElement?.setAttribute('draggable', false);
        }
    });

    document.addEventListener('mouseup', () => {
        isResizing = false;
        if (!!visitElement) {
            const heightClient = visitElement?.clientHeight;
            const heightCalc = Math.round(heightClient / heightItem) * heightItem;
            const newVisitHeight = heightCalc > heightItem ? heightCalc : heightItem;
            visitElement.style.height = `${newVisitHeight}px`;
            if (Math.abs(newVisitHeight - initialVisitHeight) >= heightItem - 3) {
                const targetColumn = visitElement.closest('.calendarColumn');
                console.log('test', visitElement, targetColumn);

                if (!!targetColumn) {
                    const targetColumnID = targetColumn?.getAttribute('id');
                    store.prevColumn = targetColumnID;
                    store.currentColumn = targetColumnID;
                }

                rechekVisitData(visitElement);
            }
            visitElement?.setAttribute('draggable', true);
        }
        visitElement = null;
    });
};
